import React, { useState } from 'react';
import TypeIt from 'typeit-react';
import * as I from '../style/Introduce.style';
import profileImg from './image/profile.jpg';

export function Introduce() {
  const [isHover, setIsHover] = useState(false);

  const intro = [
    '      ',
    '  1  ',
    '    ',
  ];

  return (
    <I.MainHeader id='introduce' aria-label=' '>
      <I.ImgBox>
        <I.ProfileImg
          src={profileImg}
          width='300'
          height='400'
          alt=' '
        />

        {isHover ? (
          <I.BackOfToMystory
            animate={{ scale: [1.1, 1.2], opacity: [0.7, 0] }}
            transition={{ repeat: Infinity }}
            style={{ width: '100px' }}
          ></I.BackOfToMystory>
        ) : (
          <I.BackOfToMystory
            animate={{ scale: [1.1, 1.4], opacity: [0.9, 0] }}
            transition={{ repeat: Infinity }}
          ></I.BackOfToMystory>
        )}

        <I.ToMystory
          to='/mystory'
          onMouseEnter={() => {
            setIsHover(true);
          }}
          onMouseLeave={() => {
            setIsHover(false);
          }}
          whileHover={{ width: '100px' }}
          role='button'
          aria-label='   '
        >
          {!isHover ? <span>+</span> : null}

          {isHover ? (
            <I.SeeMore>
              <span></span>
            </I.SeeMore>
          ) : null}
        </I.ToMystory>
      </I.ImgBox>

      <I.MainTitle>
        <span></span>
        <I.screenReadersOnly>
                ,  
          1 ,     
        </I.screenReadersOnly>
        <I.TypeItBox>
          <TypeIt
            aria-hidden='true'
            options={{ loop: true }}
            getBeforeInit={(instance) => {
              instance
                .type(intro[0])
                .pause(2000)
                .delete('*')
                .pause(500)
                .type(intro[1])
                .pause(2000)
                .delete('*')
                .pause(500)
                .type(intro[2])
                .pause(2000);

              return instance;
            }}
          ></TypeIt>
        </I.TypeItBox>
        <span></span>
        
      </I.MainTitle>
    </I.MainHeader>
  );
}
